// 导入自定义组件
import trailerStars from '../../components/trailerStars.vue';
export default {
	data() {
		return {
			trailer:{},
			trailerId:''
		}
	},
	onLoad(params) {
		// 获取上一个页面传递的参数，params：是一个对象
		this.trailerId = params.trailerId;
		// 如果没有影片id 就跳回搜索页面
		if(!this.trailerId){
			uni.switchTab({
				url: '../search/search'
			});
		}
		this.fetchMovie()
	},
	methods: {
		// 获取影片详情
		fetchMovie(){
			uni.request({
				url: this.baseURL + '/movie',
				method:"GET",
				data:{
					id:this.trailerId
				},
				success:(res) => {
					if(res.data.statusCode == 200) {
						this.trailer = res.data.data
					}
				}
			})
		},
		
	},
	components:{
		trailerStars
	}
}
